import Config from '../config';
import CryptoApis from 'cryptoapis.io';
const {
    API_KEY,
} = Config.CRYPTO_API;

const caClient = new CryptoApis(API_KEY.DEV);

class CryptoAPIsService {
  static async address(address) {
    return await caClient.BC.ETH.address.getInfo(address)
    .then((res) => {
        console.log(res);
        return res.payload;
    })
    .catch((err) => {
        console.error(err);
    });
  }

  static async generateAddress() {
    return await caClient.BC.ETH.address.generateAddress()
    .then((res) => {
        console.log(res);
        return res.payload;
    })
    .catch((err) => {
        console.error(err);
    });
  }

  static async exchangeRates(baseAssetId, quoteAssetId) {
    return await caClient.CMD.exchangeRates.getSpecificRate(baseAssetId, quoteAssetId)
    .then((res) => {
        console.log(res);
        return res.payload;
    })
    .catch((err) => {
        console.error(err);
    });
  }

  static async getTransactionByHash(tx) {
    return await caClient.BC.ETH.transaction.getTransaction(tx)
    .then((res) => {
        console.log(res);
        return res.payload;
    })
    .catch((err) => {
        console.error(err);
    });
  }

  static async createPaymentForwading(data) {
    return await caClient.BC.ETH.paymentForwarding.createPaymentForwarding(
        data.url,
        data.from,
        data.to,
        data.privateKey,
        data.confirmations,
        data.gasPrice,
        data.gasLimit,
    )
    .then((res) => {
        console.log(res);
        return res.payload;
    })
    .catch((err) => {
        console.error(err);
    });
  }

  static async createTransaction(data) {
    return await caClient.BC.ETH.transaction.newTransaction(
        data.from,
        data.to,
        data.password,
        data.value,
        data.gasPrice,
        data.gasLimit,
        null,
        data.hexData,
    )
    .then((res) => {
        console.log(res);
        return res.payload;
    })
    .catch((err) => {
        console.error(err);
    });
  }
}

export default CryptoAPIsService;
