import React from "react";
import {
	Card,
	CardBody,
	Container,
	Col,
	Row,
} from 'reactstrap';
import { AuthService } from 'services';

export default function Verify() {
    document.title = "Verify";
    const logo = require('images/zarex-logo.png');

    async function handleVerification(e) {
        e.preventDefault();
        const form = e.target;
        const reset = await AuthService.resetPassword(form.email.value);
        if (reset.success) {
            window.location = '/login';
        } else {
            alert(reset.message);
        }
    }

    return (
        <React.Fragment>
            <Card>
                <CardBody>
                    <Container>
                        <Row>
                            <Col xs={12} sm={2} md={4} />
                            <Col xs={12} sm={8} md={4}>
                                <div className="content margin-top-30">
                                    <div className="text-center">
                                        <a href="/">
                                            <img src={logo} alt="" width="200" />
                                        </a>
                                        <p className="caption text-center margin-bottom-30">Welcome to Zarex Online</p>
                                    </div>
                                    <form method="post" autoComplete="off" onSubmit={handleVerification}>
                                        <div className="form-group">
                                            <label>Enter code</label>
                                            <input type="text" name="code" className="form-control" placeholder="Verification code" />
                                        </div>
                                        <div className="form-group margin-bottom-30">
                                            <div className="form-row">
                                                <div className="col-12">
                                                    <button type="submit" className="btn btn-primary btn-block">Verify</button>
                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </Col>
                            <Col xs={12} sm={2} md={4} />
                        </Row>
                    </Container>
                </CardBody>
            </Card>
        </React.Fragment>
    );
}
