import React, { useEffect, useState } from "react";
import {
	Card,
	CardBody,
	Container,
	Col,
	Row,
} from 'reactstrap';
import { Session } from 'bc-react-session';
import HashLinkContainer from "components/HashLinkContainer";
import { AuthService, UserService } from 'services';

export default function Login() {
    const [isSubmitting, setIsSubmitting] = useState(false);

    document.title = "Login";
    const logo = require('images/zarex-logo.png');

    useEffect(() => {
		const clearSession = async () => {
			await Session.destroy();
		};
		clearSession();
	}, []);

    async function handleLogin(e) {
        e.preventDefault();
        setIsSubmitting(true);
        const form = e.target;
        const auth = await AuthService.login(form.username.value, form.password.value);
        if (auth.success) {
            if (auth.data.user['group.name'] === 'default') {
                // start react session
                await Session.start({
                    payload: auth.data,
                    expiration: 21600000, // 6 hours
                });
                // get user's complete profile
                const profile = await UserService.profile()
                .catch(err => {
                    console.error(err);
                });
                await Session.setPayload({
                    token: auth.data.token,
                    user: profile,
                });
                window.location = '/dashboard';
            } else {
                setIsSubmitting(false);
                alert('Access Denied');
            }
        } else {
            setIsSubmitting(false);
            alert(auth.message);
        }
    }

    return (
        <React.Fragment>
            <Card>
                <CardBody>
                    <Container>
                        <Row>
                            <Col xs={12} sm={2} md={4} />
                            <Col xs={12} sm={8} md={4}>
                                <div className="content margin-top-30">
                                    <div className="text-center">
                                        <a href="/">
                                            <img src={logo} alt="" width="200" />
                                        </a>
                                        <p className="caption text-center margin-bottom-30">Welcome to Zarex Online</p>
                                    </div>
                                    <form method="post" autoComplete="off" onSubmit={handleLogin}>
                                        <div className="form-group">
                                            <label>Username</label>
                                            <input type="text" name="username" className="form-control" placeholder="Email or phone" />
                                        </div>
                                        <div className="form-group margin-bottom-20">
                                            <label>Password</label>
                                            <input type="password" name="password" className="form-control" placeholder="Your password" autoComplete="new-password" />
                                        </div>
                                        <div className="form-group margin-bottom-30">
                                            <div className="form-row">
                                                <div className="col-6">
                                                    <label className="custom-control custom-checkbox">
                                                        <input type="checkbox" className="custom-control-input" checked onChange={() => {console.log('clicked')}} />
                                                        <span className="custom-control-label">Remember me</span>
                                                    </label>
                                                </div>
                                                <div className="col-6 text-right">
                                                    <HashLinkContainer to="/reset">
                                                        <a href="/reset">Forgot password?</a>
                                                    </HashLinkContainer>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="form-group margin-bottom-30">
                                            <div className="form-row">
                                                <div className="col-2" />
                                                <div className="col-8">
                                                    <button
                                                        type="submit"
                                                        className="btn btn-primary btn-block"
                                                        disabled={isSubmitting}
                                                    >
                                                        {isSubmitting ? 'Processing...' : 'Login to account'}
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                    <div className="divider" />
                                    <div className="form-group text-center">
                                        <div className="form-row">
                                            <div className="col-4">
                                                <a href="/" className="text-muted" target="_blank">About us</a>
                                            </div>
                                            <div className="col-4">
                                                <a href="/" className="text-muted" target="_blank">Contacts</a>
                                            </div>
                                            <div className="col-4">
                                                <a href="/" className="text-muted" target="_blank">Support</a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Col>
                            <Col xs={12} sm={2} md={4} />
                        </Row>
                    </Container>
                </CardBody>
            </Card>
        </React.Fragment>
    );
}
