import axios from 'axios';
import Config from '../config';
import { Session } from 'bc-react-session';

const session = Session.getSession();
let headers = {};

if (session.isValid) {
  headers = {
    'Authorization': `Bearer ${session.payload.token}`,
  }
}

class AccountsService {
  static async get() {
    return await axios({
      mode: 'no-cors',
      method: 'GET',
      headers: headers,
      url: `${Config.API.BASE_URL}account`,
      crossdomain: true,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async getAll() {
    return await axios({
      mode: 'no-cors',
      method: 'GET',
      headers: headers,
      url: `${Config.API.BASE_URL}accounts`,
      crossdomain: true,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async getBankAccounts() {
    return await axios({
      mode: 'no-cors',
      method: 'GET',
      headers: headers,
      url: `${Config.API.BASE_URL}user/bank-accounts`,
      crossdomain: true,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async getCryptoAccounts() {
    return await axios({
      mode: 'no-cors',
      method: 'GET',
      headers: headers,
      url: `${Config.API.BASE_URL}user/crypto-accounts`,
      crossdomain: true,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async getOne(id) {
    return await axios({
      mode: 'no-cors',
      method: 'GET',
      headers: headers,
      url: `${Config.API.BASE_URL}accounts/${id}`,
      crossdomain: true,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async addBankAccount(data) {
    return await axios({
      mode: 'no-cors',
      method: 'POST',
      headers: headers,
      url: `${Config.API.BASE_URL}accounts`,
      crossdomain: true,
      data,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async addCryptoAccount(data) {
    return await axios({
      mode: 'no-cors',
      method: 'POST',
      headers: headers,
      url: `${Config.API.BASE_URL}accounts`,
      crossdomain: true,
      data,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }
}

export default AccountsService;
