import axios from 'axios';
import Config from '../config';
import { Session } from 'bc-react-session';

const session = Session.getSession();
let headers = {};

if (session.isValid) {
  headers = {
    'Authorization': `Bearer ${session.payload.token}`,
  }
}

class UserService {
  static async profile() {
    return await axios({
      mode: 'no-cors',
      method: 'GET',
      headers: headers,
      url: `${Config.API.BASE_URL}user`,
      crossdomain: true,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async addresses() {
    return await axios({
      mode: 'no-cors',
      method: 'GET',
      headers: headers,
      url: `${Config.API.BASE_URL}user/addresses`,
      crossdomain: true,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async emails() {
    return await axios({
      mode: 'no-cors',
      method: 'GET',
      headers: headers,
      url: `${Config.API.BASE_URL}user/emails`,
      crossdomain: true,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async mobiles() {
    return await axios({
      mode: 'no-cors',
      method: 'GET',
      headers: headers,
      url: `${Config.API.BASE_URL}user/mobiles`,
      crossdomain: true,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }
}

export default UserService;
