import axios from 'axios';
import Config from '../config';
import { Session } from 'bc-react-session';

const session = Session.getSession();
let headers = {};

if (session.isValid) {
  headers = {
    'Authorization': `Bearer ${session.payload.token}`,
  }
}

class AuthService {
  static async login(user, password) {
    return await axios({
      mode: 'no-cors',
      method: 'POST',
      url: `${Config.API.BASE_URL}auth/login`,
      data: { user, password },
    })
    .then((json) => json.data)
    .then((res) => {
      return res;
    });
  }

  static async register(data) {
    return await axios({
      mode: 'no-cors',
      method: 'POST',
      url: `${Config.API.BASE_URL}auth/register`,
      data,
    })
    .then((json) => json.data)
    .then((res) => {
      return res;
    });
  }

  static async tokens(id) {
    return await axios({
      mode: 'no-cors',
      method: 'GET',
      headers: {},
      url: `${Config.API.BASE_URL}auth/tokens`,
    })
    .then((json) => json.data)
    .then((res) => {
      return res;
    });
  }

  static async changePassword(old_password, new_password1, new_password2) {
    return await axios({
      mode: 'no-cors',
      method: 'POST',
      url: `${Config.API.BASE_URL}auth/password/change`,
      data: { old_password, new_password1, new_password2 },
      crossdomain: true,
      headers,
    })
    .then((json) => json.data)
    .then((res) => {
      return res;
    });
  }

  static async logout() {
    return await axios({
      mode: 'no-cors',
      method: 'POST',
      url: `${Config.API.BASE_URL}auth/logout`,
      crossdomain: true,
      headers,
    })
    .then((json) => json.data)
    .then((res) => {
      return res;
    });
  }

  static async resetPassword(user) {
    return await axios({
      mode: 'no-cors',
      method: 'POST',
      url: `${Config.API.BASE_URL}auth/password/reset`,
      data: { user },
      crossdomain: true,
    })
    .then((json) => json.data)
    .then((res) => {
      return res;
    });
  }
}

export default AuthService;
