import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { Session } from 'bc-react-session';
import { Container } from 'reactstrap';
import { Navigation } from 'containers';
import { AuthService, AccountsService } from 'services';

function Breadcrumb(props) {
	const [account, setAccount] = useState({});
	const [userInfo, setUserInfo] = useState({});

	useEffect(() => {
		const session = Session.getSession();
		if (!session.isValid) {
			handleLogout();
		}
		if (session.isValid) {
			setUserInfo(session.payload.user);
		}
		const fetchData = async () => {
			const account = await AccountsService.get();
			setAccount(account);
		};
		fetchData();
	}, []);

	async function handleLogout() {
		await AuthService.logout()
		.then(() => {
			Session.destroy();
			window.localStorage.removeItem('zarexAuth');
			window.location = '/login';
		});
	}

	return (
		<React.Fragment>
			<Navigation {...userInfo} />
			<Container fluid>
				<div id="breadcrumb">
					<div className="page-heading">
						<div className="page-heading__container">
							<h1 className="title">Welcome Back, {userInfo && `${userInfo.first_name} ${userInfo.last_name}`}</h1>
						</div>
						{account.currencies &&
						<div className="page-heading__container float-right">
							<div className="account_money">
								<ul>
									<li className="crypto">
										<span>{account.currencies[0].available_balance}</span>
										<span>{account.currencies[0].currency.symbol}</span>
									</li>
									<li className="usd">
										<span>0.00</span>
										<i className="fa fa-usd"></i>
									</li>
								</ul>
							</div>
						</div>}
					</div>
				</div>
			</Container>
		</React.Fragment>
	);
}

Breadcrumb.propTypes = {
	id: PropTypes.string,
	subheading: PropTypes.string,
	title: PropTypes.string.isRequired,
	description: PropTypes.string,
	hideAppointmentButton: PropTypes.bool,
	backgroundImage: PropTypes.string,
}

export default Breadcrumb;
