import React from "react";
import {
	Card,
	CardBody,
	Container,
	Col,
	Row,
} from 'reactstrap';
import HashLinkContainer from "components/HashLinkContainer";
import { AuthService } from 'services';

export default function ResetPassword() {
    document.title = "Reset Password";
    const logo = require('images/zarex-logo.png');

    async function handleResetPassword(e) {
        e.preventDefault();
        const form = e.target;
        const reset = await AuthService.resetPassword(form.email.value);
        if (reset.success) {
            window.location = '/login';
        } else {
            alert(reset.message);
        }
    }

    return (
        <React.Fragment>
            <Card>
                <CardBody>
                    <Container>
                        <Row>
                            <Col xs={12} sm={2} md={4} />
                            <Col xs={12} sm={8} md={4}>
                                <div className="content margin-top-30">
                                    <div className="text-center">
                                        <a href="/">
                                            <img src={logo} alt="" width="200" />
                                        </a>
                                        <p className="caption text-center margin-bottom-30">Welcome to Zarex Online</p>
                                    </div>
                                    <form method="post" autoComplete="off" onSubmit={handleResetPassword}>
                                        <div className="form-group">
                                            <label>Email address</label>
                                            <input type="email" name="email" className="form-control" placeholder="Registered email" />
                                        </div>
                                        <div className="form-group margin-bottom-30">
                                            <div className="form-row">
                                                <div className="col-12 text-center">
                                                    <HashLinkContainer to="/login">
                                                        <a href="/login">Already have an account?</a>
                                                    </HashLinkContainer>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="form-group margin-bottom-30">
                                            <div className="form-row">
                                                <div className="col-2" />
                                                <div className="col-8">
                                                    <button type="submit" className="btn btn-primary btn-block">Reset password</button>
                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                    <div className="divider" />
                                    <div className="form-group text-center">
                                        <div className="form-row">
                                            <div className="col-4">
                                                <a href="/" className="text-muted" target="_blank">About us</a>
                                            </div>
                                            <div className="col-4">
                                                <a href="/" className="text-muted" target="_blank">Contacts</a>
                                            </div>
                                            <div className="col-4">
                                                <a href="/" className="text-muted" target="_blank">Support</a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Col>
                            <Col xs={12} sm={2} md={4} />
                        </Row>
                    </Container>
                </CardBody>
            </Card>
        </React.Fragment>
    );
}
