import React, { useState, useEffect } from "react";
import {
	Card,
	CardBody,
	Container,
	Col,
	Row,
} from 'reactstrap';

import Breadcrumb from "components/Breadcrumb";
import SettingsNavigation from "components/SettingsNavigation";

import {
	UserService,
} from 'services';

function EmailRow(props) {
	const {
		email,
		is_primary,
		is_verified,
	} = props;
	return (
		<tr>
			<td width="40"><button className="btn btn-light btn-icon btn-sm"><span className="fa fa-envelope"></span></button></td>
			<td><strong>{email}</strong></td>
			<td width="150">
				{is_verified
					? <div class="btn btn-outline-success btn-block disabled btn-sm">Verified</div>
					: <div class="btn btn-outline-danger btn-block disabled btn-sm">Not Verified</div>
				}
			</td>
			<td width="100"><span className="text-muted">{is_primary ? 'Default' : ''}</span></td>
			<td className="text-right">
				<button className="btn btn-light btn-icon btn-sm"><span className="fa fa-pencil"></span></button>&nbsp;
				<button className="btn btn-light btn-icon btn-sm"><span className="fa fa-times"></span></button>
			</td>
		</tr>
	);
};

function MobileRow(props) {
	const {
		number,
		is_primary,
		is_verified,
	} = props;
	return (
		<tr>
			<td width="40"><button className="btn btn-light btn-icon btn-sm"><span className="fa fa-phone"></span></button></td>
			<td><strong>{number}</strong></td>
			<td width="150">
				{is_verified
					? <div class="btn btn-outline-success btn-block disabled btn-sm">Verified</div>
					: <div class="btn btn-outline-danger btn-block disabled btn-sm">Not Verified</div>
				}
			</td>
			<td width="100"><span className="text-muted">{is_primary ? 'Default' : ''}</span></td>
			<td className="text-right">
				<button className="btn btn-light btn-icon btn-sm"><span className="fa fa-pencil"></span></button>&nbsp;
				<button className="btn btn-light btn-icon btn-sm"><span className="fa fa-times"></span></button>
			</td>
		</tr>
	);
};

export default function Security() {
	const [emails, setEmails] = useState([]);
	const [mobiles, setMobiles] = useState([]);

	useEffect(() => {
		const fetchData = async () => {
			const emails = await UserService.emails();
			const mobiles = await UserService.mobiles();
			setEmails(emails);
			setMobiles(mobiles);
		};
		fetchData();
	}, []);

    document.title = "Zarex Crypto Exchange";
    
    return (
		<React.Fragment>
			<Breadcrumb
				id="Home"
				subheading="Welcome to Zarex"
				title="Sustainable service for all and good governance"
				description="Lorem ipsum"
				backgroundImage={require("images/slide1.jpg")}
			/>
			<Container fluid>
				<Row>
					<Col xs={12} sm={3}>
						<SettingsNavigation />
					</Col>
					<Col xs={12} sm={8}>
						<Card>
							<div className="page-heading bg-transparent">
								<div className="page-heading__container">
									<h1 className="title">Security</h1>
									<p className="caption">All security settings</p>
								</div>
							</div>
							<div className="divider-text divider-text--xs">Email address</div>
							<CardBody className="pb-4">
								<div className="table-responsive">
									<table className="table table-indent-rows margin-bottom-10" style={{ minWidth: '300px' }}>
										<tbody>
											{emails.map(item => <EmailRow key={item.id} {...item} />)}
										</tbody>
									</table>
								</div>
								<div className="form-row">
									<Col sm={8}>
									<div class="form-group">
										<label>Add Email Address</label>
										<div class="input-group">
											<div class="input-group-prepend">
												<span class="input-group-text"><i class="fa fa-envelope-o"></i></span>
											</div>
											<input type="text" class="form-control" name="email" placeholder="Your email address" />
										</div>
									</div>
									</Col>
									<Col sm={4}>
										<div className="form-group">
											<label>&nbsp;</label>
											<button className="btn btn-block btn-primary">Add</button>
										</div>
									</Col>
								</div>
							</CardBody>
							<div className="divider-text divider-text--xs">Mobile number</div>
							<CardBody className="pb-4">
								<div className="table-responsive">
									<table className="table table-indent-rows margin-bottom-10" style={{ minWidth: '300px' }}>
										<tbody>
											{mobiles.map(item => <MobileRow key={item.id} {...item} />)}
										</tbody>
									</table>
								</div>
								<div className="form-row">
									<Col sm={8}>
									<div class="form-group">
										<label>Add Mobile Number</label>
										<div class="input-group">
											<div class="input-group-prepend">
												<span class="input-group-text"><i class="fa fa-phone"></i></span>
											</div>
											<input type="text" class="form-control" name="phone" placeholder="Your mobile number" />
										</div>
									</div>
									</Col>
									<Col sm={4}>
										<div className="form-group">
											<label>&nbsp;</label>
											<button className="btn btn-block btn-primary">Add</button>
										</div>
									</Col>
								</div>
							</CardBody>
						</Card>
					</Col>
				</Row>
            </Container>
		</React.Fragment>
    );
}
