import React, { useState, useEffect } from "react";
import {
	Card,
	CardBody,
	Container,
	Col,
	Row,
} from 'reactstrap';

import Breadcrumb from "components/Breadcrumb";
import TransactionRow from "components/TransactionRow";

import {
	TransactionsService,
} from '../services';

export default function Transactions() {
	const [transactions, setTransactions] = useState([]);

	document.title = "Zarex Crypto Exchange";

	useEffect(() => {
		const fetchData = async () => {
			const transactions = await TransactionsService.getAll();
			setTransactions(transactions);
		};
		fetchData();
	}, []);
    
    return (
		<React.Fragment>
			<Breadcrumb
				id="Home"
				subheading="Welcome to Zarex"
				title="Sustainable service for all and good governance"
				description="Lorem ipsum"
				backgroundImage={require("images/slide1.jpg")}
			/>
			<Container fluid>
				<Row>
					<Col xs={12}>
						<Card>
							<div className="page-heading bg-transparent">
								<div className="page-heading__container">
									<h1 className="title">Transaction history</h1>
									<p className="caption">List and overview of all transactions</p>
								</div>
							</div>
							<CardBody className="pt-4 pb-4">
								<div className="table-responsive">
									<table className="table table-indent-rows margin-bottom-10" style={{ minWidth: '500px' }}>
										<tbody>
											{transactions.map(item => <TransactionRow key={item.id} {...item} />)}
										</tbody>
									</table>
								</div>
							</CardBody>
						</Card>
					</Col>
				</Row>
            </Container>
		</React.Fragment>
    );
}
