import React from "react";

function TransactionRow(props) {
    const {
        created,
        label,
        status,
        currency,
        reference,
        total_amount,
    } = props;
    let icon = 'li-check';
    let badge = 'bg-success';
    
    if (status.toLowerCase() === 'pending') icon = 'li-hourglass';
    if (status.toLowerCase() === 'pending') badge = 'bg-success';

    return (
        <tr>
            <td width="40">
                <div className={`icon-box icon-box--bordered icon-box--rounded ${badge}`}>
                <span className={icon}></span></div>
            </td>
            <td>
                <strong>{label}</strong><br />
                <span className="text-muted">{reference && reference}</span>
            </td>
            <td width="200"><strong>{created}</strong><br /><span className="text-muted">12:23:15 GMT</span></td>
            <td width="100" className="text-right">
                <strong className="text-danger">{`-${currency.symbol}${total_amount}`}</strong><br />
                <span className="text-muted">-$1,959.22</span>
            </td>
        </tr>
    );
}

export default TransactionRow;