import React from "react";
import {
	Container,
	Nav,
	Navbar,
} from "react-bootstrap";
import { Image } from "react-bootstrap";
import { Session } from 'bc-react-session';
import HashLinkContainer from 'components/HashLinkContainer';
import { AuthService } from 'services';

// custom scrolling function so that the header doesn't overlap anchor content
const topMinusHeaderHeight = el => {
	const scrollY =
		el.offsetTop - document.getElementById("site-navbar").offsetHeight;
	window.scroll({ top: scrollY, behavior: "smooth" });
};

export default function Navigation(props) {
	const profileImage = require(`images/person_4.jpg`);
	const logoImage = require(`images/zarex-logo.png`);
	const {
		first_name,
		last_name,
		nationality,
	} = props;

	async function handleLogout() {
		await AuthService.logout()
		.then(() => {
			Session.destroy();
			window.localStorage.removeItem('zarexAuth');
			window.location = '/login';
		});
	}

	return (
		<React.Fragment>
			<Container fluid className="page__header">
				<div className="logo-holder">
					<a href="/dashboard" className="logo d-none d-lg-block">
						<Image src={logoImage} />
					</a>
					<a href="/dashboard" className="logo-text d-lg-none">
						<strong className="text-primary">ZAR</strong><strong>EX</strong>
					</a>
				</div>
				<div className="box-fluid" />
				<div className="box">
					<div className="user user--rounded user--bordered user--lg margin-bottom-0">
						<Image src={profileImage} />
						<div className="user__name">
							<strong>{first_name && first_name} {last_name && last_name}</strong><br/>
							<span className="text-muted">{nationality && nationality}</span>
						</div>
					</div>
					<button className="btn btn-icon" onClick={handleLogout}>
						<span className="li-power-switch"></span>
					</button>
				</div>
			</Container>
			<Container fluid className="page__container invert">
				<Navbar
					id="site-navbar"
					sticky="top"
					collapseOnSelect
					expand="lg"
				>
					<Navbar.Toggle aria-controls="responsive-navbar-nav" />
					<Navbar.Collapse id="responsive-navbar-nav">
						<Nav className="ml-auto">
							<HashLinkContainer scroll={topMinusHeaderHeight} to="/dashboard">
								<Nav.Link>
									<span className="icon li-home" />
									<span className="text">Dashboard</span>
								</Nav.Link>
							</HashLinkContainer>
							<HashLinkContainer scroll={topMinusHeaderHeight} to="/accounts">
								<Nav.Link>
									<span className="icon li-wallet" />
									<span className="text">Accounts</span>
								</Nav.Link>
							</HashLinkContainer>
							<HashLinkContainer scroll={topMinusHeaderHeight} to="/transactions">
								<Nav.Link>
									<span className="icon li-history2" />
									<span className="text">Transactions</span>
								</Nav.Link>
							</HashLinkContainer>
							<HashLinkContainer scroll={topMinusHeaderHeight} to="/downloads">
								<Nav.Link>
									<span className="icon li-download2" />
									<span className="text">Downloads</span>
								</Nav.Link>
							</HashLinkContainer>
							{/* <HashLinkContainer scroll={topMinusHeaderHeight} to="/referrals">
								<Nav.Link>
									<span className="icon li-group-work" />
									<span className="text">Referrals</span>
								</Nav.Link>
							</HashLinkContainer> */}
							<HashLinkContainer scroll={topMinusHeaderHeight} to="/exchange">
								<Nav.Link>
									<span className="icon li-sync" />
									<span className="text">Exchange</span>
								</Nav.Link>
							</HashLinkContainer>
							<HashLinkContainer scroll={topMinusHeaderHeight} to="/settings">
								<Nav.Link>
									<span className="icon li-cog" />
									<span className="text">Settings</span>
								</Nav.Link>
							</HashLinkContainer>
						</Nav>
					</Navbar.Collapse>
				</Navbar>
			</Container>
		</React.Fragment>
	);
}
