import React, { useState, useEffect } from "react";
import {
	Card,
	CardBody,
	Container,
	Col,
	Row,
} from 'reactstrap';
import { Breadcrumb, SettingsNavigation } from 'components';
import { UserService, AuthService } from 'services';
import Countries from 'static/countries.json';

export default function Settings() {
	const [profile, setProfile] = useState({});

	useEffect(() => {
		const fetchData = async () => {
			const profile = await UserService.profile();
			console.log(profile);
			setProfile(profile);
		};
		fetchData();
	}, []);
	
	document.title = "Settings";
	
	async function handleChangePassword(e) {
		e.preventDefault();
		const form = e.target;
		const oldPassword = form.old_password.value;
		const newPassword1 = form.new_password1.value;
		const newPassword2 = form.new_password2.value;
		await AuthService.changePassword(oldPassword, newPassword1, newPassword2)
			.then((res) => {
				console.log(res);
			});
	}

	async function handleUpdateProfile(e) {
		e.preventDefault();
		const form = e.target;
		const data = {
			email: form.email.value,
			mobile: form.mobile.value,
			birth_date: form.birth_date.value,
			id_number: form.id_number.value,
		};
		console.log(data);
	}
    
    return (
		<React.Fragment>
			<Breadcrumb
				id="Settings"
				subheading="Settings"
				title="Sustainable service for all and good governance"
				description="Lorem ipsum"
				backgroundImage={require("images/slide1.jpg")}
			/>
			<Container fluid>
				<Row>
					<Col xs={12} sm={3}>
						<SettingsNavigation />
					</Col>
					<Col xs={12} sm={9}>
						<Row>
							<Col xs={12} sm={6}>
								<Card>
									<div className="page-heading bg-transparent">
										<div className="page-heading__container">
											<h1 className="title">Profile</h1>
											<p className="caption">Your Profile information</p>
										</div>
									</div>
									<CardBody className="pt-4 pb-4">
										<form>
											<div className="form-row">
												<Col xs={12}>
													<div className="form-group">
														<label>Username</label>
														<input type="text" className="form-control" placeholder={profile.username} disabled />
													</div>
												</Col>
											</div>
											<div className="form-row">
												<Col xs={12}>
													<div className="form-group">
														<label>Change Photo</label>
														<label className="custom-file">
															<input type="file" id="file2" className="custom-file-input" /> 
															<span className="custom-file-label">Choose file</span>
														</label>
													</div>
												</Col>
											</div>
											<div className="form-row">
												<Col xs={12}>
													<div className="form-group">
														<button type="submit" className="btn btn-grad">Save Changes</button>
													</div>
												</Col>
											</div>
										</form>
									</CardBody>
								</Card>
							</Col>
							<Col xs={12} sm={6}>
								<form onSubmit={handleChangePassword}>
									<Card>
										<div className="page-heading bg-transparent">
											<div className="page-heading__container">
												<h1 className="title">Change Password</h1>
												<p className="caption">Update your password</p>
											</div>
										</div>
										<CardBody className="pt-4 pb-4">
											<div className="form-row">
												<Col xs={12}>
													<div className="form-group">
														<label>Current Password</label>
														<input type="password" name="old_password" className="form-control" placeholder="Type current password" />
													</div>
												</Col>
											</div>
											<div className="form-row">
												<Col xs={12}>
													<div className="form-group">
														<label>New Password</label>
														<input type="password" name="new_password1" className="form-control" placeholder="Type new password" />
													</div>
												</Col>
											</div>
											<div className="form-row">
												<Col xs={12}>
													<div className="form-group">
														<label>Confirm Password</label>
														<input type="password" name="new_password2" className="form-control" placeholder="Confirm new password" />
													</div>
												</Col>
											</div>
											<div className="form-row">
												<Col xs={12}>
													<div className="form-group">
														<button
															type="submit"
															className="btn btn-grad"
															disabled
														>
															Save Changes
														</button>
													</div>
												</Col>
											</div>
										</CardBody>
									</Card>
								</form>
							</Col>
						</Row>
						<Row>
							<Col xs={12}>
								<Card>
									<div className="page-heading bg-transparent">
										<div className="page-heading__container">
											<h1 className="title">Personal information</h1>
											<p className="caption">Your personal information</p>
										</div>
									</div>
									<CardBody className="pt-4 pb-4">
										<form onSubmit={handleUpdateProfile}>
											<div className="form-row">
												<Col xs={12} sm={6}>
													<div className="form-group">
														<label>First name</label>
														<input type="text" name="first_name" className="form-control" placeholder={profile.first_name} disabled />
													</div>
												</Col>
												<Col xs={12} sm={6}>
													<div className="form-group">
														<label>Last name</label>
														<input type="text" name="last_name" className="form-control" placeholder={profile.last_name} disabled />
													</div>
												</Col>
											</div>
											<div className="form-row">
												<Col xs={12} sm={6}>
													<div className="form-group">
														<label>Email</label>
														<input type="email" name="email" className="form-control" placeholder={profile.email} disabled />
													</div>
												</Col>
												<Col xs={12} sm={6}>
													<div className="form-group">
														<label>Mobile</label>
														<input type="text" name="mobile" className="form-control" placeholder={profile.mobile} disabled />
													</div>
												</Col>
											</div>
											<div className="form-row">
												<Col xs={12} sm={6}>
													<div className="form-group">
														<label>Date of birth</label>
														<input type="text" name="birth_date" className="form-control" placeholder={profile.birth_date} disabled={profile.birth_date && profile.birth_date.length > 0} />
													</div>
												</Col>
												<Col xs={12} sm={6}>
													<div className="form-group">
														<label>SAID/Passport Number</label>
														<input type="text" name="id_number" className="form-control" placeholder={profile.id_number} disabled={profile.id_number && profile.id_number.length > 0} />
													</div>
												</Col>
											</div>
											<div className="form-row">
												<Col xs={12} sm={6}>
													<div className="form-group">
														<label>Address</label>
														<input type="text" className="form-control" placeholder="Present address" />
													</div>
												</Col>
												<Col xs={12} sm={6}>
													<div className="form-group">
														<label>City</label>
														<input type="text" className="form-control" placeholder="City" />
													</div>
												</Col>
											</div>
											<div className="form-row">
												<Col sm={6}>
													<div className="form-group">
														<label>Postal/Zip Code</label>
														<input type="text" className="form-control" placeholder="Postal code" />
													</div>
												</Col>
												<Col sm={6}>
													{profile &&
													<div className="form-group">
														<label>Country</label>
														<select
															name="nationality"
															className="custom-select"
															defaultValue={profile.nationality}
														>
															{Countries && Countries.map((item) => <option key={item.code} value={item.code}>{item.name}</option>)}
														</select>
													</div>}
												</Col>
											</div>
											<div className="form-row">
												<Col xs={12}>
													<div className="form-group">
														<button className="btn btn-grad">Save Changes</button>
													</div>
												</Col>
											</div>
										</form>
									</CardBody>
								</Card>
							</Col>
						</Row>
					</Col>
				</Row>
            </Container>
		</React.Fragment>
    );
}
