import React, { useState, useEffect } from "react";
import {
	Card,
	CardBody,
	Container,
	Col,
	Row,
} from 'reactstrap';
import { Image } from "react-bootstrap";
import { Session } from 'bc-react-session';
import CurrencyFormat from 'react-currency-format';
import {
	Breadcrumb,
	Chart,
	TransactionRow,
	DownloadsCard,
	DepositCard,
	WithdrawalCard,
 } from 'components';
import {
	AccountsService,
	QRCodeService,
	TransactionsService,
} from 'services';

const user = Session.getSession().payload.user;
export default function Dashboard() {
	const [transactions, setTransactions] = useState([]);
	const [account, setAccount] = useState({});
	const [qrcode, setQrcode] = useState([]);
	const [wallet] = useState(user ? user.crypto_accounts[0] : {});
	const [balanceDefault, setBalanceDefault] = useState(0);
	const [balanceUSD, setBalanceUSD] = useState(0);
	const [balanceZAR, setBalanceZAR] = useState(0);

	useEffect(() => {
		const fetchData = async () => {
			if (user) {
				const transactions = await TransactionsService.getAll();
				const account = await AccountsService.get();
				const qrcode = await QRCodeService.get('300x300', user.crypto_accounts[0].address);
				setTransactions(transactions);
				setAccount(account);
				setQrcode(qrcode);
				setBalanceDefault(account.currencies[0].available_balance);
			}
		};
		fetchData();
	}, []);

	if (user === undefined) {
		window.location = '/login';
		return false;
	}

	document.title = "Zarex Dashboard";

	return (
		<React.Fragment>
			<Breadcrumb
				id="Dashboard"
				subheading="Welcome to Zarex"
				title="Sustainable service for all and good governance"
				description="Lorem ipsum"
				backgroundImage={require("images/slide1.jpg")}
			/>
			<Container fluid>
				<Row>
					<Col xs={12} sm={8}>
						<Card>
							<div className="page-heading bg-transparent">
								<div className="page-heading__container">
									<h1 className="title">Available Balances</h1>
									<p className="caption">Your online Zarex crypto wallet</p>
								</div>
							</div>
							<CardBody className="pt-4 pb-4">
								<Row>
									<Col sm={6} md={6} className="text-center">
										<Chart />
									</Col>
									<Col sm={6} md={6}>
										<div className="mt-6">
											<p className="text-muted mt-3"><strong>Total Balance</strong></p>
											<h1 className="text-primary">
												<CurrencyFormat
													value={balanceDefault}
													displayType={'text'}
													thousandSeparator={true}
													decimalScale={10}
													fixedDecimalScale
												/>
												&nbsp;
												{wallet.currency_code}
											</h1>
											<div className="card-inner-container">
												<div className="form-row">
													<div className="col-6">
														<h3 className="margin-bottom-0">
															<CurrencyFormat
																value={balanceUSD}
																displayType={'text'}
																thousandSeparator={true}
																prefix={'$ '}
																decimalScale={2}
																fixedDecimalScale
															/>
														</h3>
														<span className="text-muted">Balance in USD</span><br/>
													</div>
													<div className="col-6 text-right">
														<h3 className="margin-bottom-0">
															<CurrencyFormat
																value={balanceZAR}
																displayType={'text'}
																thousandSeparator={true}
																prefix={'R '}
																decimalScale={2}
																fixedDecimalScale
															/>
														</h3>
														<span className="text-muted">Balance in ZAR</span><br/>
													</div>
												</div>
											</div>
											<br/>
											<div className="text-center">
												<p><strong>To receive funds use barcode or address below</strong></p>
												<Image src={qrcode} style={{ maxWidth: '300px' }} />
												<p className="mt-3"><strong>{wallet.address}</strong></p>
											</div>
										</div>
									</Col>
								</Row>
							</CardBody>
						</Card>
						{transactions.length > 0 &&
						<Card>
							<div className="page-heading bg-transparent">
								<div className="page-heading__container">
									<h1 className="title">Latest Transactions</h1>
									<p className="caption">List of recent transactions</p>
								</div>
							</div>
							<CardBody className="pt-4 pb-4">
								<div className="table-responsive">
									<table className="table table-indent-rows margin-bottom-10" style={{ minWidth: '500px' }}>
										<tbody>
											{transactions.map((item) => <TransactionRow key={item.id} {...item} />)}
										</tbody>
									</table>
								</div>
							</CardBody>
						</Card>}
					</Col>
					<Col xs={12} sm={4}>
						<DepositCard account={account} />
						<WithdrawalCard account={account} />
						<DownloadsCard />
					</Col>
				</Row>
			</Container>
		</React.Fragment>
	);
}
