import React, { useState } from "react";
import {
	Card,
	CardBody,
	Col,
} from 'reactstrap';

function DepositCard() {
    const [depositType, setDepositType] = useState('bank');
    const [submitDisabled, setSubmitDisabled] = useState(true);
    const [currencies, setCurrencies] = useState({
        bank: [{ label: 'Rands (ZAR)', value: 'ZAR' }],
        crypto: [
            { label: 'Bitcoin (BTC)', value: 'BTC' },
            { label: 'Ethereum (ETH)', value: 'ETH' },
            { label: 'KaratGold Coin (BTC)', value: 'KBC' },
        ],
    });
    const depositTypes = [
        { label: 'Credit/debit card', value: 'bank' },
        // { label: 'Crypto account', value: 'crypto' },
    ];

    const handleSubmit = (e) => {
        e.preventDefault();
        const form = e.target;
        const data = {
            type: form.type.value,
            currency: form.currency.value,
            amount: form.amount.value,
        };
        console.log(data);
    };

    const handleTypeChange = (e) => {
        setDepositType(e.target.value);
    };

    return (
        <Card>
            <div className="page-heading bg-transparent">
                <div className="page-heading__container">
                    <h1 className="title">Deposits</h1>
                    <p className="caption">Open a new deposit</p>
                </div>
            </div>
            <CardBody className="pt-4 pb-4">
                <form onSubmit={handleSubmit}>
                    <div className="form-row">
                        <Col xs={6}>
                            <div className="form-group">
                                <label>Deposit with</label>
                                <select
                                    name="type"
                                    className="custom-select"
                                    defaultValue="bank"
                                    onChange={handleTypeChange}
                                >
                                    {depositTypes.map((item) => {
                                        return <option key={item.value} value={item.value}>{item.label}</option>
                                    })}
                                </select>
                            </div>
                        </Col>
                        <Col xs={6}>
                            <div className="form-group">
                                <div className="form-group">
                                    <label>From currency</label>
                                    <select
                                        name="currency"
                                        className="custom-select"
                                        defaultValue="ZAR"
                                    >
                                        {currencies[depositType].map((item) => {
                                            return <option key={item.value} value={item.value}>{item.label}</option>
                                        })}
                                    </select>
                                </div>
                            </div>
                        </Col>
                    </div>
                    <div className="form-row">
                        <Col xs={6}>
                            <div className="form-group">
                                <label>Amount</label>
                                <input
                                    type="text"name="amount"
                                    className="form-control"
                                    placeholder="0.00"
                                    onInput={(e) => {
                                        setSubmitDisabled(e.target.value.length === 0);
                                    }}
                                    onKeyUp={(e) => {
                                        setSubmitDisabled(e.target.value.length === 0);
                                    }}
                                />
                            </div>
                        </Col>
                        <Col xs={6}>
                            <div className="form-group">
                                <label>&nbsp;</label>
                                <button
                                    type="submit"
                                    className="btn btn-block btn-primary"
                                    disabled={submitDisabled}
                                >
                                    Continue
                                </button>
                            </div>
                        </Col>
                    </div>
                </form>
            </CardBody>
        </Card>
    );
}

export default DepositCard;
