import axios from 'axios';
import Config from '../config';
import { Session } from 'bc-react-session';

const session = Session.getSession();
let headers = {};

if (session.isValid) {
  headers = {
    'Authorization': `Bearer ${session.payload.token}`,
  }
}

class TransactionsService {
  static async getAll() {
    return await axios({
      mode: 'no-cors',
      method: 'GET',
      headers,
      url: `${Config.API.BASE_URL}transactions`,
      crossdomain: true,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async getOne(id) {
    return await axios({
      mode: 'no-cors',
      method: 'GET',
      headers,
      url: `${Config.API.BASE_URL}transactions/${id}`,
      crossdomain: true,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async createCredit(data) {
    data.tx_type = 'credit';
    data.label = 'Credit';
    return await axios({
      mode: 'no-cors',
      method: 'POST',
      headers,
      url: `${Config.API.BASE_URL}transactions/credit`,
      crossdomain: true,
      data,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async createDebit(data) {
    data.tx_type = 'debit';
    data.label = 'Debit';
    return await axios({
      mode: 'no-cors',
      method: 'POST',
      headers,
      url: `${Config.API.BASE_URL}transactions/debit`,
      crossdomain: true,
      data,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }

  static async createTransfer(data) {
    return await axios({
      mode: 'no-cors',
      method: 'POST',
      headers,
      url: `${Config.API.BASE_URL}transactions/transfer`,
      crossdomain: true,
      data,
    })
    .then((json) => json.data)
    .then((res) => {
      return res.data;
    });
  }
}

export default TransactionsService;
