import React from 'react';
import {
	Card,
	CardBody,
	Container,
	Col,
	Row,
} from 'reactstrap';
import Breadcrumb from 'components/Breadcrumb';
import { AccountsService } from 'services';

export default function AddBankAccounts(props) {
    document.title = 'Add Account';
    
    async function handleAddBankAccount(e) {
        const form = e.target;
        const data = {
            name: form.name.value,
            number: form.number.value,
            bank: form.bank.value,
            branch_name: form.branch_name.value,
            branch_code: form.branch_code.value,
            account_type: form.account_type.value,
        };
        await AccountsService.addBankAccount(data)
        .then((res) => {
            if (res.success) {
                alert('Success!');
            } else {
                alert(res.message);
            }
        });
    }
    
    async function handleAddCryptoAccount(e) {
        const form = e.target;
        const data = {
            address: form.address.value,
            crypto_type: form.crypto_type.value,
        };
        await AccountsService.addCryptoAccount(data)
        .then((res) => {
            if (res.success) {
                alert('Success!');
            } else {
                alert(res.message);
            }
        });
    }

    return (
        <React.Fragment>
            <Breadcrumb
				id='Add Account'
				subheading='Accounts'
				title='Sustainable service for all and good governance'
				description='Lorem ipsum'
				backgroundImage={require('images/slide1.jpg')}
            />
            <Container fluid>
                <Row>
                    <Col xs={12} sm={1} />
                    <Col xs={12} sm={5}>
                        <form onSubmit={handleAddBankAccount}>
                            <Card>
                                <div className="page-heading bg-transparent">
                                    <div className="page-heading__container">
                                        <h1 className="title">Add Bank Account</h1>
                                        <p className="caption">Add Bank Account for Withdrawals</p>
                                    </div>
                                </div>
                                <CardBody>
                                    <p>
                                        Please make sure your details are as menioned on your card.
                                        Otherwise, the withdrawal will fail and you may be charged
                                        a fee.
                                    </p>
                                    <div className="form-group mt-4">
                                        <label>Holder</label>
                                        <input type="text" name="name" className="form-control" placeholder="Account holder" />
                                        <small className="form-text">As written on the card</small>
                                    </div>
                                    <div className="form-group">
                                        <label>Bank</label>
                                        <input type="text" name="bank" className="form-control" placeholder="Bank name" />
                                    </div>
                                    <div className="form-row">
                                        <Col xs={8}>
                                            <div className="form-group">
                                                <label>Branch</label>
                                                <input type="text" name="branch_name" className="form-control" placeholder="Branch name" />
                                            </div>
                                        </Col>
                                        <Col xs={4}>
                                            <div className="form-group">
                                                <label>Branch/Swift code</label>
                                                <input type="text" name="branch_code" className="form-control" placeholder="Branch or swift code" />
                                            </div>
                                        </Col>
                                    </div>
                                    <div className="form-group">
                                        <label>Account type</label>
                                        <input type="text" name="account_type" className="form-control" placeholder="Type of account" />
                                    </div>
                                    <div className="form-group">
                                        <label>Account number</label>
                                        <input type="text" name="number" className="form-control" placeholder="Account number" />
                                    </div>
                                    <div className="form-group">
                                        <label>&nbsp;</label>
                                        <button type="submit" className="btn btn-block btn-primary">Add Bank Account</button>
                                    </div>
                                </CardBody>
                            </Card>
                        </form>
                    </Col>
                    <Col xs={12} sm={5}>
                        <form onSubmit={handleAddCryptoAccount}>
                            <Card>
                                <div className="page-heading bg-transparent">
                                    <div className="page-heading__container">
                                        <h1 className="title">Add Crypto Account</h1>
                                        <p className="caption">Add Crypto Account for Withdrawals</p>
                                    </div>
                                </div>
                                <CardBody>
                                    <p>
                                        Please make sure your details are as menioned on your card.
                                        Otherwise, the withdrawal will fail and you may be charged
                                        a fee.
                                    </p>
                                    <div className="form-group mt-4">
                                        <label>Name</label>
                                        <input type="text" name="name" className="form-control" placeholder="Account name" />
                                        <small className="form-text">This is for record/description purposes only</small>
                                    </div>
                                    <div className="form-group">
                                        <label>Type</label>
                                        <select name="crypto_type" className="custom-select" defaultValue="bitcoin">
                                            <option value="bitcoin">Bitcoin (BTC)</option>
                                            <option value="ethereum">Ethereum (ETH)</option>
                                            <option value="karatgoldcoin">KaratGold Coin (KBC)</option>
                                        </select>
                                    </div>
                                    <div className="form-group">
                                        <label>Address</label>
                                        <input type="text" className="form-control" placeholder="Crypto address" />
                                    </div>
                                    <div className="form-group">
                                        <label>&nbsp;</label>
                                        <button className="btn btn-block btn-primary">Add Crypto Account</button>
                                    </div>
                                </CardBody>
                            </Card>
                        </form>
                    </Col>
                </Row>
            </Container>
        </React.Fragment>
    );
}
