import React, { useState, useEffect } from 'react';
import {
	Card,
	CardBody,
	Col,
} from 'reactstrap';
import { Session } from 'bc-react-session';
import { HashLinkContainer } from 'components';
import { AccountsService, TransactionsService } from 'services';

const user = Session.getSession().payload.user;

function WithdrawalCard(props) {
    const { account } = props;
    const [withdrawalType, setWithdrawalType] = useState('bank');
    const [bankAccounts, setBankAccounts] = useState([]);
    const [cryptoAccounts, setCryptoAccounts] = useState([]);
    const [submitDisabled, setSubmitDisabled] = useState(true);
    const withdrawalTypes = [
        { label: 'Credit/debit card', value: 'bank' },
        { label: 'Crypto account', value: 'crypto' },
    ];

	useEffect(() => {
		const fetchData = async () => {
            const bankAccounts = await AccountsService.getBankAccounts();
            const cryptoAccounts = await AccountsService.getCryptoAccounts();
            setBankAccounts(bankAccounts);
            setCryptoAccounts(cryptoAccounts);
		};
		fetchData();
    }, []);

    async function handleSubmit(e) {
        e.preventDefault();
        const form = e.target;
        const data = {
            // subtype,
            // note,
            // metadata,
            note: 'Withdrawal Request',
            // reference,
            amount: form.amount.value,
            // fee,
            total_amount: form.amount.value,
            balance: account.currencies[0].available_balance,
            account_id: account.id,
            company_id: user.company_id,
            currency: account.currencies[0].currency,
            source_transaction: form.account.value,
            destination_transaction: form.account.value,
        }
        await TransactionsService.createDebit(data)
            .then((res) => {
                console.log(res);
            })
    };

    const handleTypeChange = (e) => {
        setWithdrawalType(e.target.value);
    };

    return (
        <Card>
            <div className="page-heading bg-transparent">
                <div className="page-heading__container">
                    <h1 className="title">Withdraw</h1>
                    <p className="caption">Transfer funds for withdrawal</p>
                </div>
                <div className="page-heading__container float-right">
                    <HashLinkContainer to="/accounts/add">
                        <button className="btn btn-light">Add account</button>
                    </HashLinkContainer>
                </div>
            </div>
            <CardBody className="pt-4 pb-4">
                <form onSubmit={handleSubmit}>
                    <div className="form-row">
                        <Col xs={6}>
                            <div className="form-group">
                                <label>Withdraw to</label>
                                <select
                                    name="type"
                                    className="custom-select"
                                    defaultValue="bank"
                                    onChange={handleTypeChange}
                                >
                                    {withdrawalTypes.map((type) => {
                                        return <option key={type.value} value={type.value}>{type.label}</option>
                                    })}
                                </select>
                            </div>
                        </Col>
                        <Col xs={6}>
                            <div className="form-group">
                                <label>Account</label>
                                <select
                                    name="account"
                                    className="custom-select"
                                >
                                    <option value="">Select Account</option>
                                    {withdrawalType === 'bank' ? 
                                    bankAccounts.map((bank) => {
                                        return (<><option key={bank.id} value={bank.id}>{bank.name} ({bank.number})</option></>)
                                    }) :
                                    cryptoAccounts.map((crypto) => {
                                        return (<><option key={crypto.id} value={crypto.id}>{crypto.address})</option></>)
                                    })}
                                </select>
                            </div>
                        </Col>
                    </div>
                    <div className="form-row">
                        <Col xs={6}>
                            <div className="form-group">
                                <label>Amount</label>
                                <input
                                    type="text"
                                    name="amount"
                                    className="form-control"
                                    placeholder="0.00"
                                    onInput={(e) => {
                                        setSubmitDisabled(e.target.value.length === 0);
                                    }}
                                    onKeyUp={(e) => {
                                        setSubmitDisabled(e.target.value.length === 0);
                                    }}
                                />
                            </div>
                        </Col>
                        <Col xs={6}>
                            <div className="form-group">
                                <label>&nbsp;</label>
                                <button
                                    type="submit"
                                    className="btn btn-block btn-primary"
                                    disabled={submitDisabled}
                                >
                                    Continue
                                </button>
                            </div>
                        </Col>
                    </div>
                    <div className="card-inner-container mt-3">
                        <p className="subtitle mt-1">
                            Pressing the confirm button means you have read and accept
                            the withdrawal terms and conditions, as well as any charges
                            associated with it.
                            <br/>
                            <br/>
                            You may be asked to provide some
                            information about yourself  that is our payment providers
                            requirement.
                        </p>
                    </div>
                </form>
            </CardBody>
        </Card>
    );
}

export default WithdrawalCard;
