import React, { useState } from "react";
import {
	Card,
	CardBody,
	Container,
	Col,
	Row,
} from 'reactstrap';
import { AuthService } from 'services';
import Countries from 'static/countries.json';

export default function Register() {
    document.title = "Register";
    const logo = require('images/zarex-logo.png');
    const [subscribeCheck, setSubscribeCheck] = useState(false);

    async function handleRegister(e) {
        e.preventDefault();
        const form = e.target;
        const data = {
            // referrer: form.referrer.value,
            email: form.email.value,
            mobile: form.mobile.value,
            password: form.password.value,
            // confirm_password: form.confirm_password.value,
            nationality: form.nationality.value,
            // newsletter_subscribe: subscribeCheck,
        };
        const reg = await AuthService.register(data);
        if (reg.success) {
            window.location = '/login';
        } else {
            alert(reg.message);
        }
    }

    return (
        <React.Fragment>
            <Card>
                <CardBody>
                    <Container>
                        <Row>
                            <Col xs={12} sm={2} md={4} />
                            <Col xs={12} sm={8} md={4}>
                                <div className="content margin-top-30">
                                    <div className="text-center">
                                        <a href="/">
                                            <img src={logo} alt="" width="200" />
                                        </a>
                                        <p className="caption text-center margin-bottom-30">Welcome to Zarex Online</p>
                                    </div>
                                    <form method="post" autoComplete="off" onSubmit={handleRegister}>
                                        {/* <div className="form-group">
                                            <label>Referral's phone</label>
                                            <input type="text" name="referrer" className="form-control" placeholder="Phone number of your introducer" />
                                        </div> */}
                                        <div className="form-group margin-bottom-20">
                                            <label>Nationality</label>
                                            <select name="nationality" className="custom-select" defaultValue="ZA">
                                                {Countries && Countries.map((item) => <option key={item.code} value={item.code}>{item.name}</option>)}
                                            </select>
                                        </div>
                                        <div className="form-group">
                                            <label>Email</label>
                                            <input type="email" name="email" className="form-control" placeholder="Email address" />
                                        </div>
                                        <div className="form-group">
                                            <label>Mobile</label>
                                            <input type="tel" name="mobile" className="form-control" placeholder="Mobile number" />
                                        </div>
                                        <div className="form-group margin-bottom-20">
                                            <label>Password</label>
                                            <input type="password" name="password" className="form-control" placeholder="Your password" autoComplete="new-password" />
                                            <small className="form-text">Use at least 8 characters, 1 number, 1 symbol &amp; 1 uppercase letter</small>
                                        </div>
                                        {/* <div className="form-group margin-bottom-20">
                                            <label>Confirm Password</label>
                                            <input type="password" name="confirm_password" className="form-control" placeholder="Confirm password" autoComplete="new-password" />
                                        </div> */}
                                        {/* <div className="form-group margin-bottom-30">
                                            <div className="form-row">
                                                <div className="col-12">
                                                    <label className="custom-control custom-checkbox">
                                                        <input
                                                            type="checkbox"
                                                            name="subscribe"
                                                            className="custom-control-input"
                                                            checked={subscribeCheck}
                                                            onChange={() => {
                                                                setSubscribeCheck(!subscribeCheck);
                                                            }}
                                                        />
                                                        <span className="custom-control-label">Want the latest news, tips and promos?</span>
                                                    </label>
                                                </div>
                                            </div>
                                        </div> */}
                                        <div className="form-group margin-bottom-30">
                                            <div className="form-row">
                                                <div className="col-12">
                                                    <button
                                                        type="submit"
                                                        className="btn btn-primary btn-block"
                                                    >
                                                        Create account
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                    <div className="divider" />
                                    <div className="form-group text-center">
                                        <div className="form-row">
                                            <div className="col-12">
                                                By signing up I agree to the <a href="/" className="text-muted" target="_blank">terms of use</a> and processing of
                                                my personal data as stated in the <a href="/" className="text-muted" target="_blank">privacy policy</a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Col>
                        </Row>
                    </Container>
                </CardBody>
            </Card>
        </React.Fragment>
    );
}
