import React from 'react';
import PropTypes from 'prop-types'
import { Doughnut } from 'react-chartjs-3';

const chartData = {
	labels: [
		'Deposit',
		'Exchange',
		'Withdrawal',
	],
	datasets: [{
		data: [300, 50, 100],
		backgroundColor: [
            '#23c99d',
            '#16a3fe',
            '#112272',
		],
		hoverBackgroundColor: [
            '#23c99d',
            '#36A2EB',
            '#112272',
		],
	}],
};

function Chart(props) {
    return (
        <React.Fragment>
            <Doughnut
                data={chartData}
                height={500}
                legend={{
                    "display": true,
                    "position": "bottom",
                    "fullWidth": true,
                    "reverse": false,
                }} 
            />
        </React.Fragment>
    );
}

Chart.propTypes = {
    type: PropTypes.string,
};

export default Chart;